				duration: '',
				currentTime: 0,
				videoContext: '',
				buttonimage: '',
				status: 0,
				start: '',
				end: '',
				showbigimage: false,
				albumpath: '',
				myanswer: {},
				ADC_ARRAY: [],
				shijuantype: 0,
				taskid: '',
				result_dui: '',
				result_cuo: '',
				showjiandabigimage: false,
				jianda_bigimage: '',
				isedit: false,
				selectimage: [],
				localimage: [],
				kongbai: true
			}
		},
		onReady() {


			// 锁定屏幕方向 横屏
		},

		onLoad(option) {
			uni.setNavigationBarTitle({
				title: option.name,
			});
			this.ADC_ARRAY = ['A', 'B', 'C', 'D'];
			this.info = JSON.parse(decodeURIComponent(option.info));
			// console.log(this.info);
			this.reload();
			this.videoContext = uni.createVideoContext('myVideo');
			this.buttonimage = '../../static/voice.png';
			this.albumpath = '../../static/task_add.png';
			this.result_dui = '../../static/test_YES.png';
			this.result_cuo = '../../static/test_NO.png';


			// #ifdef APP-PLUS
			setTimeout(function() {
				plus.screen.unlockOrientation();
				plus.screen.lockOrientation('landscape-primary');
			}, 1000);
			// #endif

			// setTimeout(function() {
			// 	that.kongbai = false;
			// }, 1000);


		},
		methods: {
			reload() {
				this.taskid = this.info.id;
				this.shijuantype = parseInt(this.info.status);
				this.content = this.info.content;
				this.answer = this.info.answer;
				this.type = this.info.type;
				var array = this.answer;
				for (let i = 0; i < array.length; i++) {
					var item = array[i];
					if (item.type == 2) {
						item.duoxuan_Y = [];
						for (let i = 0; i < this.ADC_ARRAY.length; i++) {
							var dic = {};
							dic.value = this.ADC_ARRAY[i];
							dic.status = 0;
							item.duoxuan_Y.push(dic);
						}
					}
					if (item.type == 5) {
						item.duoxuan_N = [];
						for (let i = 0; i < this.ADC_ARRAY.length; i++) {
							var dic = {};
							dic.value = this.ADC_ARRAY[i];
							dic.status = 0;
							item.duoxuan_N.push(dic);
						}

					}
					this.answer[i] = item;
					this.$set(this.answer, i, item);
				}
				if (this.shijuantype == 0) {
					var dic = {};
					dic.isok = 0;
					for (let i = 0; i < array.length; i++) {
						var item = array[i];
						item.rs_user = dic;
						this.answer[i] = item;
						this.$set(this.answer, i, item);
					}
				}
				for (let i = 0; i < this.answer.length; i++) {
					var dic = {};
					dic.audio = '';
					dic.audio_time = '';
					dic.img = '';
					dic.rs = [];
					if (this.shijuantype == 1 || this.shijuantype == 3) {
						dic.img = this.answer[i].rs_user.img;
						dic.rs = this.answer[i].rs_user.rs;
					}
					this.myanswer[i + 1] = dic;
					this.selectimage[i] = '';
					if (this.shijuantype == 1 || this.shijuantype == 3) {
						var item = this.answer[i];
						var image = item.rs_user.img;
						if (image.length > 5) {
							this.localimage[i] = this.answer[i].rs_user.img;
							this.selectimage[i] = this.answer[i].rs_user.img;
						} else {
							this.localimage[i] = '../../static/task_add.png';
						}
					} else {
						this.localimage[i] = '../../static/task_add.png';
					}
				}
				console.log(this.selectimage);
			},
			getTime() {
				let yy = new Date().getFullYear();
				let mm = new Date().getMonth() + 1;
				let dd = new Date().getDate();
				let hh = new Date().getHours();
				let mf = new Date().getMinutes() < 10 ? '0' + new Date().getMinutes() : new Date().getMinutes();
				let ss = new Date().getSeconds() < 10 ? '0' + new Date().getSeconds() : new Date().getSeconds();
				return yy + mm + dd + hh + mf + ss;
			},
			//填空题
			tiankonginput(event) {
				var text = event.detail.value;
				var allID = event.target.id;
				let index = parseInt(allID.substring(0, 1));
				let index2 = parseInt(allID[2]);
				var array = [];
				array[index2] = text;
				var dic = {};
				dic.audio = '';
				dic.audio_time = '';
				dic.img = '';
				dic.rs = array;
				this.myanswer[index + 1] = dic;
				console.log(this.myanswer);
			},
			//简答题
			textinputing(event) {
				var text = event.detail.value;
				let index = parseInt(event.target.id);
				console.log(event);
				//组件本地答案数组
				var dic = {};
				dic.audio = '';
				dic.audio_time = '';
				var imagename = this.selectimage[index];
				if (imagename == null) {
					dic.img = '';
				} else {
					dic.img = imagename;
				}
				dic.rs = text;
				this.myanswer[index + 1] = dic;
				console.log(this.myanswer);
			},
			//对错题
			panduan_dui(item, index) {

				if (item.selected_panduan_dui == true) {
					item.selected_panduan_dui = false;
				} else {
					item.selected_panduan_dui = true;
				}
				item.selected_panduan_cuo = false;
				this.answer[index] = item;
				this.$set(this.answer, index, item);

				//组件本地答案数组
				var dic = {};
				dic.audio = '';
				dic.audio_time = '';
				dic.img = '';
				dic.rs = '1';
				this.myanswer[index + 1] = dic;
				// console.log(this.myanswer);

			},
			panduan_cuo(item, index) {

				if (item.selected_panduan_cuo == true) {
					item.selected_panduan_cuo = false;
				} else {
					item.selected_panduan_cuo = true;
				}
				item.selected_panduan_dui = false;
				this.answer[index] = item;
				this.$set(this.answer, index, item);

				//组件本地答案数组
				var dic = {};
				dic.audio = '';
				dic.audio_time = '';
				dic.img = '';
				dic.rs = '0';
				this.myanswer[index + 1] = dic;
				// console.log(this.myanswer);
			},
			//单选
			danxuan_ABCD(item, index, item2) {
				item.danxuan = item2;
				this.answer[index] = item;
				this.$set(this.answer, index, item);
				console.log(this.answer);
				//组件本地答案数组
				var dic = {};
				dic.audio = '';
				dic.audio_time = '';
				dic.img = '';
				if (item2 == 'A') {
					dic.rs = '0';
				} else if (item2 == 'B') {
					dic.rs = '1';
				} else if (item2 == 'C') {
					dic.rs = '2';
				} else if (item2 == 'D') {
					dic.rs = '3';
				}
				this.myanswer[index + 1] = dic;
				// console.log(this.myanswer);
			},
			//定项多选
			duoxuan_Y_ABCD(item, index, item2, index2) {
				if (item2.status == 0) {
					item2.status = 1;
				} else {
					item2.status = 0;
				}
				item.duoxuan_Y[index2] = item2;
				this.answer[index] = item;
				this.$set(this.answer, index, item);
				//组件本地答案数组
				var dic = {};
				dic.audio = '';
				dic.audio_time = '';
				dic.img = '';
				var string = '';
				for (let i = 0; i < 4; i++) {
					var subitem = item.duoxuan_Y[i];
					if (subitem.status == 1) {
						if (i == 0) {
							string = i;
						} else {
							string += ',' + i;
						}
					}
				}
				dic.rs = string;
				this.myanswer[index + 1] = dic;
				// console.log(this.myanswer);
			},
			//不定项多选
			duoxuan_N_ABCD(item, index, item2, index2) {
				if (item2.status == 0) {
					item2.status = 1;
				} else {
					item2.status = 0;
				}
				item.duoxuan_N[index2] = item2;
				this.answer[index] = item;
				this.$set(this.answer, index, item);
				//组件本地答案数组
				var dic = {};
				dic.audio = '';
				dic.audio_time = '';
				dic.img = '';
				var string = '';
				for (let i = 0; i < 4; i++) {
					var subitem = item.duoxuan_N[i];
					if (subitem.status == 1) {
						if (i == 0) {
							string = i;
						} else {
							string += ',' + i;
						}
					}
				}
				dic.rs = string;
				this.myanswer[index + 1] = dic;
				// console.log(this.myanswer);
			},
			submit_edit() {
				console.log('订正作业');
				this.isedit = true;
			},
			submit() {
				console.log(this.myanswer);
				var url = '';
				if (this.isedit == false) {
					url = 'Task.SetTask';
				} else {
					url = 'Task.UpTask'
				}
				uni.showLoading({
					title: '正在提交..',
					mask: false
				});
				var jsonstr = JSON.stringify(this.myanswer);
				console.log(jsonstr);
				let gData = getApp().globalData;
				var dic = {};
				dic.uid = gData.userinfo.id,
					dic.token = gData.userinfo.token,
					dic.taskid = this.taskid,
					dic.result = jsonstr,
					uni.request({
						url: gData.site_url + url,
						method: 'GET',
						data: dic,
						success: res => {
							this.isedit = false;
							uni.hideLoading();
							console.log(res);
							uni.showToast({
								title: res.data.data.msg,
								icon: 'none'
							});
							if (res.data.data.code == 0) {
								this.getTaskMessage();
							}
						},
						fail() {
							uni.hideLoading();
						},

					});
			},
			getTaskMessage() {
				let gData = getApp().globalData;
				var that = this;
				// 获取作业	
				uni.request({
					url: gData.site_url + 'Task.GetTask',
					method: 'GET',
					data: {
						'uid': gData.userinfo.id,
						'token': gData.userinfo.token,
						'taskid': this.taskid
					},
					success: res => {

						if (res.data.data.code == 0) {
							var info = res.data.data.info[0];
							this.info = info;
							that.reload();
						} else {
							uni.showToast({
								title: res.data.data.msg,
								icon: 'none'
							});
						}
					},

				});
			},
			deletealbum(index) {
				console.log(this.selectimage);
				this.selectimage.splice(index, 1);
				this.localimage[index] = '../../static/task_add.png';
				var dic = {};
				dic = this.myanswer[index + 1];
				dic.img = '';
				this.myanswer[index + 1] = dic;
				this.$set(this.selectimage, index, this.selectimage[index]);
				this.$set(this.localimage, index, this.localimage[index]);
				console.log(this.selectimage);
			},
			decypt(code) {
				var newcode = '';
				var str = '1ecxXyLRB.COdrAi:q09Z62ash-QGn8VFNIlb=fM/D74WjS_EUzYuw?HmTPvkJ3otK5gp&';
				for (var i = 0; i < code.length; i++) {
					var codeIteam = code[i];
					for (var j = 0; j < str.length; j++) {
						var stringIteam = str[j];
						if (codeIteam == stringIteam) {
							if (j == 0) {
								newcode += str[str.length - 1];
							} else {
								newcode += str[j - 1];
							}
						}
					}
				}
				return newcode;
			},
			openalbum(index) {
				if (this.localimage[index].length >0 && this.localimage[index] != '../../static/task_add.png'){
					this.show_jiandabigimage(this.localimage[index]);
					return;
				};
				
				var that = this;
				uni.chooseImage({
					count: 1,
					success(res) {
						console.log(res.tempFilePaths[0]);
						that.albumpath = res.tempFilePaths[0];


						uni.showLoading({
							title: '上传中...',
							mask: false
						});
						var path = that.albumpath;
						let gData = getApp().globalData;
						uni.request({

							url: gData.site_url + 'Upload.GetQiniuToken',
							method: 'POST',
							data: {
								'uid': getApp().globalData.userinfo.id,
								'token': getApp().globalData.userinfo.token
							},
							success: res2 => {
								uni.hideLoading();
								if (res2.data.data.code == 0) {
									var QiNiutoken = that.decypt(res2.data.data.info[0].token);
									var name = 'UNIAPP' + that.getTime() + 'answer.png';
									console.log(name);
									qiniuUploader.upload(path, res => {
										that.selectimage[index] = name;
										that.localimage[index] = path;
										var dic = {};
										dic = that.myanswer[index + 1];
										console.log(dic);
										dic.img = name;
										that.myanswer[index + 1] = dic;
										that.$set(that.selectimage, index, that.selectimage[index]);
										that.$set(that.localimage, index, that.localimage[index]);
									}, error => {
										uni.showToast({
											title: '上传失败，请重试',
											icon: 'none'
										});
										console.log('上传失败');
										console.log(res);
									}, {
										region: 'ECN',
										domain: getApp().globalData.qiniuimageurl,
										key: name,
										uptoken: QiNiutoken,
									});
								}
							},
							fail() {
								uni.hideLoading();
								uni.showToast({
									title: '提交失败',
									icon: 'none'
								});
							},
						});
					}
				});
			},
			playvideo(url) {
				console.log(url);
				uni.navigateTo({
					url: '../homework_info/home_video?t_video=' + url,
				});
			},
			scalebig() {
				this.showbigimage = true;
			},
			scalesmall() {
				this.showbigimage = false;
			},
			show_jiandabigimage(image) {
				this.jianda_bigimage = image;
				this.showjiandabigimage = true;
			},
			jiandan_hide() {
				this.showjiandabigimage = false;
			},
			back() {
				uni.navigateBack({
					delta: 1
				});
			},
			pause() {
				if (this.status == 0) {
					this.status = 1;
					this.videoContext.play();
					this.buttonimage = '../../static/voice2.png';
				} else {
					this.status = 0;
					this.videoContext.pause();
					this.buttonimage = '../../static/voice.png';
				}
			},
			timeupdate(event) {
				if (event.detail.detail) {
					this.currentTime = event.detail.detail.currentTime;
					this.duration = event.detail.detail.duration;
					this.start = this.calcTimer(this.currentTime);
					this.end = this.calcTimer(this.duration);
				} else {
					this.currentTime = event.detail.currentTime;
					this.duration = event.detail.duration;
					this.start = this.calcTimer(event.detail.currentTime);
					this.end = this.calcTimer(event.detail.duration);
				}
			},
			// 拖动进度条
			sliderChange(data) {
				this.videoContext.seek(data.detail.value);
			},
			//拖动中
			sliderChanging(data) {
				this.currentTime = data.detail.value;
				this.start = this.calcTimer(data.detail.value);
			},
			loadedmetadata(data) {
				this.duration = data.detail.duration;
				this.start = this.calcTimer(data.detail.duration);
				console.log('音频加载完成');
			},
			calcTimer(timer) {
				if (timer === 0 || typeof timer !== 'number') {
					return '00:00'
				}
				let mm = Math.floor(timer / 60)
				let ss = Math.floor(timer % 60)
				if (mm < 10) {
					mm = '0' + mm
				}
				if (ss < 10) {
					ss = '0' + ss
				}
				return mm + ':' + ss
			},
		}
	}
</script>

<style>
	@import url("/packageA/pages/homework_info/homework_info.css");
</style>
